
















































	export default {
		data() {
			return {
				kfInfo: null,
        wechatFlag: false
			}
		},
		computed: {
      config() {
        return this.$store.state.config
      },
      defaultAvatar() {
        return '../../static/images/avatar.png'
      }
    },
    onLoad() {
      this.$http({
        url: this.$apis.kf,
        method: 'GET',
        data: null,
        cb: (err, data) => {
          if (!err && data.success) {
            this.kfInfo = data.data
          } else {
            uni.showToast({
            	title: data.message,
              icon: 'none'
            })
          }
        }
      })
    },
		methods: {
			call(num) {
        if (!num) {
          uni.showToast({
          	title: '用户未填写手机号码',
            icon: 'none'
          })
        } else {
          uni.makePhoneCall({
          	phoneNumber: num
          })
        }
      },
      copy(str) {
        uni.setClipboardData({
        	data: str,
          success: () => {
            uni.showModal({
              title: '复制成功',
              content: '微信已复制，请打开微信查找添加',
              showCancel: false
            })
          },
          fail: () => {
            uni.showModal({
              title: '复制失败',
              content: str,
              showCancel: false
            })
          },
          complete: () => {
          	this.hideWx()
          }
        })
      },
      showWx() {
        this.wechatFlag = true
      },
      hideWx() {
      	this.wechatFlag = false
      }
		}
	}
